.search-info {
  position: fixed;
  z-index: 999;
  color: #c0392b;
  background: #f1c40f;
  top: 0;
  left: 0;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  opacity: .0;
  -webkit-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s;
}

.search-info--active {
  opacity: 1;
}

.react-selectize.root-node {
  width: 100%;
}



